import logo from "@app/images/logo.svg";
import navbarLogo from "@app/images/navbar-logo.svg";
import appcatlogo from "@app/images/appcat-logo.png";

type ThemeType = "default" | "appcat";
const defaultTheme: ThemeType = "default";

type ThemeListType = {
  [key in ThemeType]: {
    name: string;
    logoSrc: string;
    logoNavbarSrc: string;
    faviconSrc?: string;
    websiteURL: string;
    documentationURL: string;
    migrationTarget: string;
  };
};

const themeList: ThemeListType = {
  "default": {
    name: "Konveyor",
    logoSrc: logo,
    logoNavbarSrc: navbarLogo,
    websiteURL: "https://konveyor.io/",
    documentationURL: "https://konveyor.io/community",
    migrationTarget: "Red Hat Middleware, cloud and containers",
  },
  "appcat": {
    name: "Azure Migrate application and code assessment for Java",
    logoSrc: appcatlogo,
    logoNavbarSrc: appcatlogo,
    faviconSrc: appcatlogo,
    websiteURL: "https://learn.microsoft.com/en-us/azure/migrate/appcat",
    documentationURL: "https://learn.microsoft.com/en-us/azure/migrate/appcat/java",
    migrationTarget: "Azure Cloud and containers",
  }
};

export const Theme =
  themeList[((window as any)["THEME"] as ThemeType) || defaultTheme];
